<template>
  <div>
    <!-- 搜索条件 -->
    <div class="search clear">
      <el-form :inline="true" size="mini">
        <el-form-item label="文章id">
          <el-input placeholder="文章id" clearable></el-input>
        </el-form-item>
        <el-form-item label="图片名称">
          <el-input placeholder="图片名称" clearable></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary">搜索</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!-- 表格 -->
    <div class="table">
      <el-table
        :data="tableData"
        size="small"
        height="100%"
        border
        style="width: 100%; height:100%;"
      >
        <el-table-column prop="artID" label="文章id"></el-table-column>
        <el-table-column prop="pic" label="图片预览">
          <template slot-scope="scope">
            <el-image style="width: 100px; height: 100px" :src="scope.row.pic" :fit="fit"></el-image>
          </template>
        </el-table-column>
        <el-table-column prop="title" label="标题"></el-table-column>
        <el-table-column prop="picName" label="图片名称"></el-table-column>
        <el-table-column label="操作" width="120">
          <template slot-scope="scope">
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >编辑</el-button>
            <el-button
              @click.native.prevent="deleteRow(scope.$index, tableData)"
              type="text"
              size="small"
            >删除</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>

    <!-- 分页 -->
    <div class="pagination">
      <el-pagination background layout="prev, pager, next, total" :total="1000"></el-pagination>
    </div>
  </div>
</template>

<script>
import { tableData } from "../../../demo/art/artpiclist";
export default {
  name: "art_artpiclist",
  data() {
    return {
      tableData
    };
  }
};
</script>

